import Gender from './Gender';
import Tool from './Tool';
import { probability, buildableString } from '../utils/types';

export type postOrgasmPayload = {
	text: string,
	actionText: buildableString,
	tools?: Tool[],
	genders: Gender[],
	chance: probability,
	singleAction?: boolean,
	canOrgasm?: boolean,
}

class PostOrgasm {

	protected _text: string;
	protected _actionText: buildableString;
	protected _genders: Gender[];
	protected _chance: probability;

	constructor(payload: postOrgasmPayload) {
		this._text = payload.text;
		this._actionText = payload.actionText;
		this._genders = payload.genders;
		this._chance = payload.chance;
	}

	public get text(): string {
		return this._text;
	}

	public get actionText(): buildableString {
		return this._actionText;
	}

	public get genders(): Gender[] {
		return this._genders;
	}

	public get chance(): probability {
		return this._chance;
	}

}

export default PostOrgasm;
