import React from 'react';
import Preferences from './components/Preferences/Preferences';
import Game from './components/Game/Game';
import Randomizer from './utils/Randomizer';

export interface AppState {
	gender: string,
	tools: string[],
	actions: string[],
	endings: string[],
	postOrgasms: string[],
}

class App extends React.Component<{}, AppState> {

	public state: AppState = {
		gender: 'male',
		tools: [],
		actions: [],
		endings: [],
		postOrgasms: [],
	}

	componentDidMount() {
		this.getPreferences();
	}

	protected getPreferences = () => {
		const getArrayParam = (value: string) => value.split(',').filter(value => value);

		const urlParams = new URLSearchParams(window.location.search);

		const gender = urlParams.get('gender');

		if (gender !== null) {
			this.setGender(gender, false);
		}

		const tools = urlParams.get('tools');

		if (tools !== null) {
			this.setTools(getArrayParam(tools), false);
		}

		const actions = urlParams.get('actions');

		if (actions !== null) {
			this.setActions(getArrayParam(actions), false);
		}

		const endings = urlParams.get('endings');

		if (endings !== null) {
			this.setEndings(getArrayParam(endings), false);
		}

		const postOrgasms = urlParams.get('postOrgasms');

		if (postOrgasms !== null) {
			this.setPostOrgasms(getArrayParam(postOrgasms), false);
		}

		const seed = urlParams.get('seed');

		if (seed !== null) {
			Randomizer.seed = seed;
		}
	}

	protected setPreferences = () => {
		const searchParams = Object.entries(this.state).reduce<any>((params, [key, value]) => {
			if (!value || (Array.isArray(value) && value.length === 0)) {
				return params;
			}

			if (Array.isArray(value)) {
				params.append(key, value.join(',').replace(/(^,{1,}|,{1,}$)/g, ''));
			} else {
				params.append(key, value);
			}

			return params;
		}, new URLSearchParams());

		searchParams.set('seed', Randomizer.seed);

		const url = `?${searchParams.toString()}`;

		window.history.replaceState(null, 'preference', url);
	}

	protected setPreferencesIfTrue = (condition: boolean) => () => {
		if (!condition) {
			return;
		}

		this.setPreferences();
	}

	protected setGender = (option: string, setPref: boolean = true) => {
		this.setState({
			gender: option
		}, this.setPreferencesIfTrue(setPref));
	}

	protected setTools = (options: string[], setPref: boolean = true) => {
		this.setState({
			tools: options
		}, this.setPreferencesIfTrue(setPref));
	}

	protected setActions = (options: string[], setPref: boolean = true) => {
		this.setState({
			actions: options
		}, this.setPreferencesIfTrue(setPref));
	}

	protected setEndings = (options: string[], setPref: boolean = true) => {
		this.setState({
			endings: options
		}, this.setPreferencesIfTrue(setPref));
	}

	protected setPostOrgasms = (options: string[], setPref: boolean = true) => {
		this.setState({
			postOrgasms: options
		}, this.setPreferencesIfTrue(setPref));
	}

	public render() {
		return (
			<div className={'bg-gray-300'}>
				<header className={'bg-gray-800 flex justify-center items-center h-64 md:h-96'}>
					<h1 className={'text-4xl bold text-white'}>JOI game</h1>
				</header>

				<div className={'-mt-10 md:-mt-24 px-2'}>
					<div
						className={[
							'min-h-screen bg-white mx-auto w-full rounded-t-lg shadow-xl',
							'p-4',
							'sm:p-10',
						].join(' ')}
						style={{ maxWidth: '1200px' }}
					>

						<Preferences
							{...this.state}
							setGender={this.setGender}
							setTools={this.setTools}
							setActions={this.setActions}
							setEndings={this.setEndings}
							setPostOrgasms={this.setPostOrgasms}
						/>

						<Game {...this.state} />

					</div>
				</div>
			</div>
		)
	}

}

export default App;
