import React from 'react';
import OptionContainer from './OptionContainer/OptionContainer';
import OptionTitle from './OptionTitle/OptionTitle';
import Input from 'advanced-react-input';
import gameSettings from '../../settings/game';
import env from '../../settings/env';
import genders from '../../settings/genders';
import tools from '../../settings/tools';
import Gender from '../../entity/Gender';
import Tool from '../../entity/Tool';
import Action from '../../entity/Action';
import Ending from '../../entity/Ending';
import PostOrgasm from '../../entity/PostOrgasm';
import { getKeyFromObject } from '../../utils/utils';
import { AppState } from '../../App';

export interface PreferencesProps extends AppState {
	setGender: (option: string) => void,
	setTools: (options: string[]) => void,
	setActions: (options: string[]) => void,
	setEndings: (options: string[]) => void,
	setPostOrgasms: (options: string[]) => void,
}

class Preferences extends React.Component<PreferencesProps> {
	protected get hasGender(): boolean {
		return !!this.props.gender;
	}

	protected get hasTools(): boolean {
		return this.props.tools.length > 0;
	}

	protected get hasActions(): boolean {
		return this.props.actions.length > 0;
	}

	protected get hasEndings(): boolean {
		return this.props.endings.length > 0;
	}

	protected get canPostOrgasm(): boolean {
		return this.endings.filter((ending) => ending.canPostOrgasm).length > 0;
	}

	protected get genderOptions() {
		return Object.entries(genders).map(([key, gender]) => ({ label: gender.text, value: key }));
	}

	protected get toolOptions() {
		return Object.entries(tools)
			.filter(([, tool]) => tool.genders.includes(this.gender))
			.map(([key, tool]) => ({ label: tool.text, value: key }));
	}

	protected get actionOptions() {
		return Object.entries(gameSettings.actions)
			.filter(([, action]) => action.genders.includes(this.gender))
			.filter(([, action]) => {
				if (!action.tools) {
					return true;
				}

				return action.tools.some(tool => this.tools.includes(tool));
			})
			.map(([key, action]) => ({ label: action.text, value: key }));
	}

	protected get endingOptions() {
		return Object.entries(gameSettings.endings)
			.filter(([, ending]) => ending.genders.includes(this.gender))
			.filter(([, ending]) => {
				if (!ending.tools) {
					return true;
				}

				return ending.tools.some(tool => this.tools.includes(tool));
			})
			.map(([key, ending]) => ({ label: ending.text, value: key }));
	}

	protected get postOrgasmOptions() {
		return Object.entries(gameSettings.postOrgasms)
			.filter(([, postOrgasm]) => postOrgasm.genders.includes(this.gender))
			.map(([key, postOrgasm]) => ({ label: postOrgasm.text, value: key }));
	}

	protected get gender(): Gender {
		return genders[this.props.gender];
	}

	protected get tools(): Tool[] {
		return this.props.tools
			.map(option => tools[option])
			.filter(postOrgasm => postOrgasm)
	}

	protected get actions(): Action[] {
		return this.props.actions
			.map(option => gameSettings.actions[option])
			.filter(postOrgasm => postOrgasm)
	}

	protected get endings(): Ending[] {
		return this.props.endings
			.map(option => gameSettings.endings[option])
			.filter(postOrgasm => postOrgasm)
	}

	protected get postOrgasms(): PostOrgasm[] {
		return this.props.postOrgasms
			.map(option => gameSettings.postOrgasms[option])
			.filter(postOrgasm => postOrgasm)
	}

	protected getValue = (list: any, data: unknown | unknown[]): string | string[] | undefined  => {
		if (Array.isArray(data)) {
			return data
				.map(value => getKeyFromObject(list, value))
				.filter(value => value !== null) as string[];
		}

		return getKeyFromObject(list, data) || undefined;
	}

	public render() {
		return (
			<div>
				<OptionContainer>
					<OptionTitle>What is your gender ? (This will affect what options are available to you)</OptionTitle>

					<Input
						type={'radio'}
						options={this.genderOptions}
						defaultValue={this.props.gender}
						onChange={this.props.setGender}
					/>
				</OptionContainer>

				{this.hasGender && (
					<OptionContainer>
						<OptionTitle>Choose body parts/toys you would like to use</OptionTitle>

						<Input
							type={'checkbox'}
							options={this.toolOptions}
							defaultValue={this.props.tools}
							onChange={this.props.setTools}
						/>
					</OptionContainer>
				)}

				{this.hasGender && this.hasTools && (
					<OptionContainer>
						<OptionTitle>Choose what you would like to do to yourself/toys</OptionTitle>

						<Input
							type={'checkbox'}
							options={this.actionOptions}
							defaultValue={this.props.actions}
							onChange={this.props.setActions}
						/>
					</OptionContainer>
				)}

				{this.hasGender && this.hasTools && this.hasActions && (
					<OptionContainer>
						<OptionTitle>Choose how you would like this game to end</OptionTitle>

						<Input
							type={'checkbox'}
							options={this.endingOptions}
							defaultValue={this.props.endings}
							onChange={this.props.setEndings}
						/>
					</OptionContainer>
				)}

				{env.showHidden && this.hasGender && this.hasTools && this.hasActions && this.hasEndings && this.canPostOrgasm && (
					<OptionContainer>
						<OptionTitle>Need a bit of challenge ? Choose what you would like to do post orgasm</OptionTitle>

						<Input
							type={'checkbox'}
							options={this.postOrgasmOptions}
							defaultValue={this.props.postOrgasms}
							onChange={this.props.setPostOrgasms}
						/>
					</OptionContainer>
				)}
			</div>
		)
	}
}

export default Preferences;
