export type timeObject = { h: number, m: number, s: number };

class Time {

	public static secondsToTime(secs: number): timeObject {
		secs = Math.round(secs);

		const hours = Math.floor(secs / (60 * 60));
		const divisorForMinutes = secs % (60 * 60);
		const minutes = Math.floor(divisorForMinutes / 60);
		const divisorForSeconds = divisorForMinutes % 60;
		const seconds = Math.ceil(divisorForSeconds);

		return {
			h: hours,
			m: minutes,
			s: seconds
		}
	}

	public static hmsToClock(timeObject: timeObject, format?: { m?: boolean, s?: boolean }): string {
		format = {
			m: true,
			s: true,
			...format,
		};

		const sec = (timeObject.s < 10 && format.s) ? `0${timeObject.s}` : timeObject.s;
		const min = (timeObject.m < 10 && format.m) ? `0${timeObject.m}` : timeObject.m;

		if (timeObject.h === 0) {
			return `${min}:${sec}`;
		}

		return `${timeObject.h}:${min}:${sec}`;
	}

}

export default Time;
