import React, { useState, useRef } from 'react';

export interface GameContentProps {
	background: React.ReactChild,
}

const heightToWidthRatio = (9/16) * 100;

const GameContent: React.FC<GameContentProps> = (props) => {
	const node = useRef(null);
	const [isFullScreen, setFullScreen] = useState(false);

	const toggleFullScreen = () => {
		const willBeFullScreen = !isFullScreen;
		const bodyClass = 'overflow-hidden';

		if (willBeFullScreen) {
			document.body.classList.add(bodyClass);
		} else {
			document.body.classList.remove(bodyClass);
		}

		setFullScreen(willBeFullScreen);
	}

	return (
		<div className={[
			'gameContent',
			'bg-black flex justify-center items-center',
			isFullScreen ? 'fixed top-0 right-0 bottom-0 left-0' : '',
		].join(' ')}>
			<div
				ref={node}
				className={[
					'relative bg-black w-full',
					!isFullScreen ? 'h-0' : 'h-screen',
				].join(' ')}
				style={(
					!isFullScreen
						? { paddingBottom: `${heightToWidthRatio}%` }
						: { }
				)}
			>

				<div className={'absolute w-full h-full'}>
					{props.background}
				</div>

				<button
					className={[
						'top-0 right-0 p-2 text-white bg-black bg-opacity-3',
						isFullScreen ? 'fixed' : 'absolute',
					].join(' ')}
					onClick={toggleFullScreen}
				>
					<span>FullScreen</span>
				</button>

				{props.children}

			</div>
		</div>
	)
}

export default GameContent;
