import Gender from './Gender';
import Tool from './Tool';
import { probability, buildableString } from '../utils/types';

export type actionPayload = {
	text: string,
	actionText: buildableString,
	tools?: Tool[],
	genders: Gender[],
	chance: probability,
	singleAction?: boolean,
	canOrgasm?: boolean,
}

class Action {

	protected _text: string;
	protected _actionText: buildableString;
	protected _tools?: Tool[];
	protected _genders: Gender[];
	protected _chance: probability;
	protected _singleAction?: boolean;
	protected _canOrgasm?: boolean;

	constructor(payload: actionPayload) {
		this._text = payload.text;
		this._actionText = payload.actionText;
		this._tools = payload.tools;
		this._genders = payload.genders;
		this._chance = payload.chance;
		this._singleAction = payload.singleAction;
		this._canOrgasm = payload.canOrgasm;
	}

	public get text(): string {
		return this._text;
	}

	public get actionText(): buildableString {
		return this._actionText;
	}

	public get tools(): Tool[] | undefined {
		return this._tools;
	}

	public get genders(): Gender[] {
		return this._genders;
	}

	public get chance(): probability {
		return this._chance;
	}

	public get singleAction(): boolean {
		return this._singleAction ?? false;
	}

	public get canOrgasm(): boolean {
		if (this._canOrgasm === undefined && this.singleAction) {
			return false;
		}
		
		return this._canOrgasm ?? true;
	}

}

export default Action;
