import React from 'react';

export interface PornEmbedProps {
	className?: string,
	touchable: boolean,
	onMount?: CallableFunction,
}

export interface PornEmbedState {
	source?: string,
}

class PornEmbed extends React.Component<PornEmbedProps, PornEmbedState> {

	static defaultProps = {
		touchable: true,
	}

	public state: PornEmbedState = {
		source: undefined,
	}

	public componentDidMount() {
		setTimeout(() => {
			this.props.onMount?.();
		}, 0);
	}

	protected pornhubUrl = (videoId: string) => {
		return `https://www.pornhub.com/embed/${videoId}`;
	}

	protected xvideosUrl = (videoId: string) => {
		return `https://www.xvideos.com/embedframe/${videoId}`;
	}

	public setSource = (src: string): void => {
		let source;

		if (src.includes('pornhub.com')) {
			const urlParam = src.match(/viewkey=ph[a-zA-Z0-9]*/)?.[0];

			if (!urlParam) {
				return;
			}

			const videoId = urlParam.replace('viewkey=', '');

			source = this.pornhubUrl(videoId);
		} else if (/^(ph|pornhub):[a-zA-Z0-9]*/.test(src)) {
			const videoId = src.replace(/^(ph|pornhub):/, '');

			source = this.pornhubUrl(videoId);
		} else if (src.includes('xvideos.com')) {
			const urlParam = src.match(/\/video[0-9]*\//)?.[0];

			if (!urlParam) {
				return;
			}

			const videoId = urlParam.replace('video', '').replace(/\//g, '');

			source = this.xvideosUrl(videoId);
		} else if (/^(xvid|xvideos):[a-zA-Z0-9]*/.test(src)) {
			const videoId = src.replace(/^(xvid|xvideos):/, '');

			source = this.xvideosUrl(videoId);
		}

		this.setState({ source });
	}

	public render() {
		if (!this.state.source) {
			return null;
		}

		return (
			<iframe
				className={[
					'pornEmbed',
					this.props.className,
					!this.props.touchable ? 'pointer-events-none' : 'pointer-events-auto',
				].join(' ')}
				title={'porn'}
				src={this.state.source}
				frameBorder={0}
				scrolling={'no'}
				allowFullScreen={false}
			/>
		)
	}
}

export default PornEmbed;
