import React from 'react';
import Time from '../../utils/Time';
import Speed from '../../entity/Speed';
import gameSettings from '../../settings/game';
import { gameAction } from '../../utils/types';

export interface GameOverlayProps {
	className?: string,
	isVisible: boolean,
	isEnding: boolean,
	animationSpeed: string,
	totalTime: number,
	currentSpeed: Speed,
	nextSpeed: Speed,
	currentAction: gameAction,
	nextAction: gameAction,
	remainingTime: number,
}

const timeDisplayFormat = (time: number) => {
	const timeObject = Time.secondsToTime(time);

	return Time.hmsToClock(timeObject, { m: false });
}

const getRemainingTime = (time: number): number => {
	return Math.ceil(time);
}

const getCurrentSpeedBoxColor = (currentSpeed: Speed) => {
	switch (currentSpeed) {
		case gameSettings.speed.edge:
		case gameSettings.speed.extreme:
			return 'bg-red-600';

		case gameSettings.speed.hard:
			return 'bg-yellow-500';

		case gameSettings.speed.medium:
			return 'bg-green-500';

		case gameSettings.speed.slow:
			return 'bg-blue-500';

		case gameSettings.speed.gentle:
			return 'bg-purple-500';

		default:
			return 'bg-black';
	}
}

const getNextSpeedTextColor = (nextSpeed: Speed) => {
	switch (nextSpeed) {
		case gameSettings.speed.edge:
		case gameSettings.speed.extreme:
			return 'text-red-600';

		case gameSettings.speed.hard:
			return 'text-yellow-500';

		case gameSettings.speed.medium:
			return 'text-green-500';

		case gameSettings.speed.slow:
			return 'text-blue-500';

		case gameSettings.speed.gentle:
			return 'text-purple-500';

		default:
			return 'text-white';
	}
}

const GameOverlay: React.FC<GameOverlayProps> = ({
	className,
	isVisible,
	isEnding,
	animationSpeed,
	totalTime,
	currentSpeed,
	nextSpeed,
	currentAction,
	nextAction,
	remainingTime,
}) => {
	const currentSpeedBoxColor = getCurrentSpeedBoxColor(currentSpeed);
	const nextSpeedTextColor = getNextSpeedTextColor(nextSpeed);
	remainingTime = getRemainingTime(remainingTime);

	const totalTimeBox = (
		<div className={'absolute p-2 md:p-3 top-0 left-0 bg-black bg-opacity-70'}>
			<div className={'text-sm md:text-md bold'}>{timeDisplayFormat(totalTime)}</div>
		</div>
	);

	const actionBox = currentAction && (
		<div className={'absolute p-2 md:p-3 bottom-0 left-0 bg-black bg-opacity-70'}>
			{!isEnding && nextAction && nextSpeed && (
				<div className={'text-xs bold'}>
					{nextSpeed !== gameSettings.speed.stop && (
						<>
							<span className={'hidden md:inline'}>Up next: </span>
							<span>{nextAction.text}</span>
						</>
					)}
					{nextSpeed !== gameSettings.speed.none && (
						<span className={nextSpeedTextColor}> {`(${nextSpeed.text})`}</span>
					)}
				</div>
			)}

			<div className={'text-md md:text-2xl bold'}>{currentAction.text}</div>
		</div>
	);

	const speedBox = (
		<div className={[
			'absolute bottom-0 right-0 bg-opacity-70 overflow-hidden',
			'w-14 h-14',
			'md:w-24 md:h-24',
			currentSpeedBoxColor,
		].join(' ')}>
			<div className={'relative flex flex-col justify-center items-center w-full h-full'}>
				<div
					className={'absolute w-full h-full bg-gray-100 bg-opacity-30 animate-bounce'}
					style={{ animationDuration: animationSpeed }}
				/>
				{currentSpeed !== gameSettings.speed.none && (
					<div className={'z-10 text-xs md:text-lg'}>{currentSpeed.text}</div>
				)}
				<div className={'z-10 text-md md:text-2xl bold'}>{remainingTime}</div>
			</div>
		</div>
	);

	return (
		<div className={[
			'gameOverlay',
			'transition absolute w-full h-full pointer-events-none text-white',
			!isVisible ? 'opacity-0 visibility-none' : '',
		].join(' ')}>
			<div className={'w-full h-full p-2 md:p-3'}>
				<div className={'relative w-full h-full'}>

					{totalTimeBox}

					{actionBox}

					{speedBox}

				</div>
			</div>
		</div>
	)
}

export default GameOverlay;
