class SuperMath {

	static linear(x: number, a: number, c: number = 0): number {
		return a * x + c;
	}

	static exponential(x: number, a: number, b: number, c: number = 0): number {
		return a * (b ** x) + c;
	}

	static smallestDivisor(number: number): number {
		if (number % 2 === 0) {
			return 2;
		}

		for (let i = 3; i * i <= number; i += 2) {
			if (number % i === 0) {
				return i;
			}
		}

		return number;
	}

	static toPercent(number: number, decimal: number = 2): number {
		const value = Math.round(number * (10 ** decimal)) / 100;

		if (Number.isNaN(value)) {
			return 0;
		}

		return value;
	}

}

export default SuperMath;
