import { probability, durationRange } from '../utils/types';

export type speedPayload = {
	text: string,
	bps: number,
	chance: probability,
	duration: durationRange,
	followUpWhitelist?: () => Speed[],
	followUpBlacklist?: () => Speed[],
}

class Speed {

	protected _text: string;
	protected _bps: number;
	protected _chance: probability;
	protected _duration: durationRange;
	protected _followUpWhitelist?: () => Speed[];
	protected _followUpBlacklist?: () => Speed[];

	constructor(payload: speedPayload) {
		this._text = payload.text;
		this._bps = payload.bps;
		this._chance = payload.chance;
		this._duration = payload.duration;
		this._followUpWhitelist = payload.followUpWhitelist;
		this._followUpBlacklist = payload.followUpBlacklist;
	}

	public get text(): string {
		return this._text;
	}

	public get bps(): number {
		return this._bps;
	}

	public get chance(): probability {
		return this._chance;
	}

	public get duration(): durationRange {
		return this._duration;
	}

	public get followUpWhitelist(): Speed[] | undefined {
		return this._followUpWhitelist?.();
	}

	public get followUpBlacklist(): Speed[] | undefined {
		return this._followUpBlacklist?.();
	}

}

export default Speed;
